import React, { Component } from 'react';
import axios from 'axios';

class OpportunitySkill extends Component {

    state = {
        skillName: "",
        skillID: "",
        opportunityID: "",
        token: "",
        error: null,
        url: "",
        studentLogin: null
    }

    async componentDidMount(){
        const {skill, opportunityID, skillID, skillName} = this.props;
        this.setState({opportunityID, skillID, skillName});
        if (skill==="required"){
            this.setState({url: "https://careers-server.herokuapp.com/removeRequiredSkillFromOpportunity/"});
        }
        else if (skill==="desired"){
            this.setState({url: "https://careers-server.herokuapp.com/removeDesiredSkillFromOpportunity/"});
        }
        const authenticationToken = await localStorage.getItem("token");
        if (authenticationToken!==null){
        this.setState({token: authenticationToken});
        }
        const login = await localStorage.getItem("login");
        if (login==="Student"){
            this.setState({studentLogin: true});
        }
    }

    async removeSkill(){
        try{
        const results = await axios.get(`${this.state.url}?opportunityID=${this.state.opportunityID}&skillID=${this.state.skillID}&authenticationToken=${this.state.token}`);
        if (results.data==="Done"){
            this.props.reloadComponent();
        }
        else{
            this.setState({error: results.data})
        }
        }
        catch (error){
            var seperateErrorMessages = error.response.data.split(":");
            this.setState({error: seperateErrorMessages[3]});
            setTimeout(() => {this.setState({error: null});}, 3000);
        }
    }

    render() {
        return (
            <React.Fragment>
            {this.state.error ? (<div style={{color: "red"}}>{this.state.error}</div>) : (
            <div className="col-xl-2 col-lg-2 col-md-3 col-sm-6 mb-5" style={{marginTop: "-35px"}}><div style={{width:"auto", border: "1px solid lightGrey", borderRadius: "5px"}}>
                {this.state.skillName} {this.state.studentLogin ? (null) : (<React.Fragment>
                   <br></br><button style={{marginBottom:"4px"}} onClick={this.removeSkill.bind(this)}>Remove</button></React.Fragment>)}</div>
                    
            </div>
            )}
            </React.Fragment>
        );
    }
}

export default OpportunitySkill;