import React, { Component } from 'react';
import axios from "axios";
import { Redirect } from 'react-router-dom';

export default class ViewSkillInfo extends Component {

    state = {
        name: null,
        type: "",
        sector: "",
        skillID: "",
        error: null,
        deleteError: null,
        authenticationToken: "",
        skillDeleted: null
    };
    
    async componentDidMount(){
        const {id} = await this.props.match.params;
        var url = `https://careers-server.herokuapp.com/viewSkills/?skillID=${id}`;
        const token = localStorage.getItem("token");
        if (token!=null){
        const login = await localStorage.getItem("login");
        if (login==="Student"||login==="Admin"||login==="Contact")
        {
           url = `${url}&authenticationToken=${token}`;
           this.setState({authenticationToken: token});
        }
    }
        try{
        const results = await axios.get(url);
        await this.setState({
                         skillID: id,
                        name: results.data["skillList"][0].name,
                       type : results.data["skillList"][0].category.skillType,
                sector: results.data["skillList"][0].category.businessSector});
        }
        catch(error){
            var seperateErrorMessages = error.response.data.split(":");
            this.setState({error: seperateErrorMessages[3]})
        }
    }


    async delete() {
        try {
          const results = await axios.get(
            `https://careers-server.herokuapp.com/removeSkill/?skillID=${this.state.skillID}&authenticationToken=${this.state.authenticationToken}`
          );
          //console.log(results.data); //for testing
    
          if (results.data === "Done") {
            await localStorage.setItem(
              "redirect",
              "/matching/skillList"
            );
            this.setState({ skillDeleted: true });
          }
        } catch (error) {
          var seperateErrorMessages = error.response.data.split(":");
          this.setState({ deleteError: seperateErrorMessages[3] });
        }
      }
    

    render() {
        return (
            <React.Fragment>
                {this.state.skillDeleted ? (
          <Redirect to="/recordDeleted"></Redirect>
        ) : (null)}
               {this.state.name ? ( 
            <div>
                <h2> {this.state.name}</h2><br/>
                Type: {this.state.type}<br/><br/>
                Sector: {this.state.sector}<br/>
                <br/>
                {this.state.deleteError ? (<div style={{color: "red"}}>{this.state.deleteError}</div>) : (
                <div>
              <button className="deleteButton" onClick={this.delete.bind(this)}>
                Delete Skill
              </button>
            </div>
            )}
            </div>
            ) : this.state.error ? (<div style={{color: "red"}}>{this.state.error}</div>) : (<div>No skill found</div>)}
            </React.Fragment>
        );
    }
}
