import React, { Component } from 'react';
import axios from "axios";
import OpportunitySkill from "./OpportunitySkill";

export default class ViewOpportunityInfo extends Component {

    state = {
        title: null,
        salary: "",
        country: null,
        region: null,
        verified: false,
        active: false,
        dateVerified: "",
        dateActivated: "",
        dateCreated: "",
        closingDate: "",
        details: "",
        howToApply: "",
        organisationHiddenFromStudents: false,
        organisationName: null,
        opportunityID: "",
        authenticationToken: "",
        error: null,
        newClosingDate: null,
        closingDateMessage: null,
        closingDateError: null,
        studentLogin: null,
        requiredSkillList: null,
        desiredSkillList: null,
        notSetSkillList: null,
        skillID: "",
        loading: true,
        requiredSkillError: null,
        desiredSkillError: null
    };
    
    async componentDidMount(){
         
        const {id} = await this.props.match.params;
        this.setState({opportunityID: id});
        var url = `https://careers-server.herokuapp.com/viewOpportunities/?opportunityID=${id}`;
        const token = await localStorage.getItem("token");
        if (token!=null){
          url = `${url}&authenticationToken=${token}`
          await this.setState({authenticationToken: token});
        }
        const login = await localStorage.getItem("login");
        if (login==="Student"){
          this.setState({studentLogin: true})
        }
        try{
        const results = await axios.get(url);
        await this.setState({title: results.data["opportunityList"][0].title,
                       salary : results.data["opportunityList"][0].salary,
                country: results.data["opportunityList"][0].location.country,
                region: results.data["opportunityList"][0].location.region,
                verified: results.data["opportunityList"][0].verified,
                active: results.data["opportunityList"][0].active,
                details: results.data["opportunityList"][0].details,
                howToApply: results.data["opportunityList"][0].howToApply,
                dateCreated: results.data["opportunityList"][0].dateCreated,
                closingDate: new Date(results.data["opportunityList"][0].closingDate).toISOString().substr(0, 10),
                opportunityID: results.data["opportunityList"][0].opportunityID.opportunityID,
                organisationHiddenFromStudents: results.data["opportunityList"][0].contact.organisation.isHiddenFromStudents,
                loading: null});
                //console.log(this.state.closingDate);
        if(this.state.verified){this.setState({dateVerified: results.data["opportunityList"][0].dateVerified})};
        if(this.state.active){this.setState({dateActivated: results.data["opportunityList"][0].dateActivated})};
        if(!this.state.organisationHiddenFromStudents){this.setState({organisationName: results.data["opportunityList"][0].contact.organisation.name})};

        const skillResults = await axios.get(`https://careers-server.herokuapp.com/getOpportunitySkillList/?opportunityID=${id}&authenticationToken=${token}`);
        if ( skillResults.data["opportunityRequiredSkills"][0]!=null){
          await this.setState({requiredSkillList: skillResults.data["opportunityRequiredSkills"]});
        }
        else {
          this.setState({requiredSkillList: null});
        }
        if ( skillResults.data["opportunityDesiredSkills"][0]!=null){
        await this.setState({desiredSkillList: skillResults.data["opportunityDesiredSkills"]});
        }
        else {
          this.setState({desiredSkillList: null});
        }
        if ( skillResults.data["opportunitySkillsNotSet"][0]!=null){
        await this.setState({notSetSkillList: skillResults.data["opportunitySkillsNotSet"],
                                skillID: skillResults.data["opportunitySkillsNotSet"][0].skillID.id});
        }
        else {
          this.setState({notSetSkillList: null});
        }
        //console.log(this.state.requiredSkillList);
        //console.log(this.state.desiredSkillList);
        //console.log(this.state.notSetSkillList);
      }
      catch(error){
        this.setState({error: error.response.data.split(":")[3]});
      }

    }

    async reloadComponent(){
      this.componentDidMount();
    }
    
    async unverify() {
      try{
        const results = await axios.get(
          `https://careers-server.herokuapp.com/undoVerifyOpportunity/?id=${this.state.opportunityID}&authenticationToken=${this.state.authenticationToken}`
        );
        //console.log(results.data); //for testing
    
        if (results.data === "Done") {
          this.setState({ verified: false,
                       active: false });
        }
      }
      catch(error){
        this.setState({error: error.response.data.split(":")[3]});
      }
      }
    
      async verify() {
        try{
        const results = await axios.get(
          `https://careers-server.herokuapp.com/verifyOpportunity/?id=${this.state.opportunityID}&authenticationToken=${this.state.authenticationToken}`
        );
        //console.log(results.data); //for testing
    
        if (results.data === "Done") {
          const results = await axios.get(`https://careers-server.herokuapp.com/viewOpportunities/?opportunityID=${this.state.opportunityID}`+
          `&authenticationToken=${this.state.authenticationToken}`);
          this.setState({ verified: true,
                  dateVerified: results.data["opportunityList"][0].dateVerified});
          
        }
      }    
    catch(error){
      this.setState({error: error.response.data.split(":")[3]});
    }
      }
    
      async deactivate() {
        try{
        const results = await axios.get(
          `https://careers-server.herokuapp.com/undoActivateOpportunity/?id=${this.state.opportunityID}`+
          `&authenticationToken=${this.state.authenticationToken}`
        );
        //console.log(results.data); //for testing
    
        if (results.data === "Done") {
          this.setState({ active: false });
        }
      }
      catch(error){
        this.setState({error: error.response.data.split(":")[3]});
      }
      }
    
      async activate() {
        try{
        const results = await axios.get(
          `https://careers-server.herokuapp.com/activateOpportunity/?id=${this.state.opportunityID}&authenticationToken=${this.state.authenticationToken}`
        );
        //console.log(results.data); //for testing
    
        if (results.data === "Done") {
          const results = await axios.get(`https://careers-server.herokuapp.com/viewOpportunities/?opportunityID=${this.state.opportunityID}`+
          `&authenticationToken=${this.state.authenticationToken}`);
          this.setState({ active: true,
                  dateActivated: results.data["opportunityList"][0].dateActivated});
        }
      }
      catch(error){
        this.setState({error: error.response.data.split(":")[3]});
      }
      }

      async updateClosingDate(e) {
        var date = await new Date(e);
        //console.log(date);
        await this.setState({ newClosingDate: date.getTime(),
                            closingDateComparison: date.toISOString().substr(0, 10) });
        //console.log(this.state.newClosingDate);
        this.setState({ closingDateTemp: e });
      }

      async changeClosingDate() {
        if (this.state.newClosingDate!==null){
          if(this.state.closingDateComparison===this.state.closingDate){
            this.setState({closingDateError: "Error - The date selected is already set as the current closing date"});
            setTimeout(() => {this.setState({closingDateError: null});}, 3000);
          }
          else{
            try{
          await axios.get(`https://careers-server.herokuapp.com/changeOpportunityClosingDate/?id=${this.state.opportunityID}
          &authenticationToken=${this.state.authenticationToken}
                          &closingDate=${this.state.newClosingDate}`);
          this.setState({closingDateMessage: "Closing date updated"});
          setTimeout(() => {this.setState({closingDateMessage: null});}, 3000);
          }
          catch(error){
            var seperateErrorMessages = error.response.data.split(":");
             this.setState({ closingDateError: seperateErrorMessages[3] });
          }
        }
      }
        else{
          this.setState({closingDateError: "Error - Please select a date from the field to update the closing date"});
          setTimeout(() => {this.setState({closingDateError: null});}, 3000);
        }
      }

      async updateSkillID(e){
        await this.setState({skillID: e});
        //console.log(this.state.skillID);
      }

      async addRequiredSkill(){
        try{
          const addResults = await axios.get(`https://careers-server.herokuapp.com/addRequiredSkillToOpportunity/?opportunityID=${this.state.opportunityID}`+
          `&skillID=${this.state.skillID}&authenticationToken=${this.state.authenticationToken}`);
          if (addResults.data==="Done"){
             this.componentDidMount();
          }
          else{
             this.setState({requiredSkillError: addResults.data})
          }
          }
        catch (addError){
              var seperateErrorMessages = addError.response.data.split(":");
              this.setState({requiredSKillError: seperateErrorMessages[3]});
          }
      }

      async addDesiredSkill(){
        try{
          const addResults = await axios.get(`https://careers-server.herokuapp.com/addDesiredSkillToOpportunity/?opportunityID=${this.state.opportunityID}`+
          `&skillID=${this.state.skillID}&authenticationToken=${this.state.authenticationToken}`);
          if (addResults.data==="Done"){
              this.componentDidMount();
          }
          else{
              this.setState({desiredSkillError: addResults.data})
          }
          }
          catch (addError){
              var seperateErrorMessages = addError.response.data.split(":");
              this.setState({desiredSkillError: seperateErrorMessages[3]});
          }
      }

    render() {
      const { state } = this;
        return (
            <React.Fragment>
              {this.state.loading ? (<h1>Loading Opportunity</h1>) : (<React.Fragment>
              {this.state.error ? (<div style={{color:"red", width:"100%"}}>{this.state.error}</div>) : (null)}
               {this.state.title ? ( 
            <div>
                <h3> {this.state.title}</h3>
                <h5 style={{marginBottom: '7px'}}>{this.state.organisationName ? (this.state.organisationName) : (null)}</h5>
                {this.state.salary}<br/>
                {this.state.country}, {this.state.region} <br/>
                {this.state.studentLogin ? (<div style={{fontWeight: "bold"}}>Closing Date: {this.state.closingDate.split("T")[0].split("-")[2]}
                         /{this.state.closingDate.split("T")[0].split("-")[1]}
                         /{this.state.closingDate.split("T")[0].split("-")[0]}</div>) :(<React.Fragment>
                <div className='toggleTablet' style={{marginTop: '10px'}}>
                Verified?
                <React.Fragment>
              {this.state.verified ? (
                <span style={{color:'darkGreen', fontWeight: 'bold'}}>
                  Yes. Verified on  {this.state.dateVerified.split("T")[0].split("-")[2] +
                         '/' + this.state.dateVerified.split("T")[0].split("-")[1] +
                         '/' + this.state.dateVerified.split("T")[0].split("-")[0]}<br/>
                  <button onClick={this.unverify.bind(this)}>Unverify</button>
                </span>
              ) : (
                <span style={{color:'red', fontWeight: 'bold'}}>
                No <br/><button onClick={this.verify.bind(this)}>Verify</button>
                </span>
              )}
            </React.Fragment>
            </div>
            <div className='toggleTablet' style={{width: '240px'}}>
            Active?{" "}
            <React.Fragment>
              {this.state.active ? (
                <span style={{color:'darkGreen', fontWeight: 'bold'}}>
                 Yes. Activated on  {this.state.dateActivated.split("T")[0].split("-")[2] +
                         '/' + this.state.dateActivated.split("T")[0].split("-")[1] +
                         '/' + this.state.dateActivated.split("T")[0].split("-")[0]}<br/><button onClick={this.deactivate.bind(this)}>Deactivate</button>
                </span>
              ) : (
                <span style={{color:'red', fontWeight: 'bold'}}>
                  No <br/><button onClick={this.activate.bind(this)}>Activate</button>
                </span>
              )}
            </React.Fragment>
            
            </div>
            <div className='toggleTablet' style={{width: '240px'}}>
            <form onSubmit={this.onSubmit}>
            <label>
              Closing Date:
              <input
                style={{ marginLeft: "20px" }}
                type="date"
                defaultValue={this.state.closingDate}
                value={state.closingDateTemp}
                
                onChange={(e) => this.updateClosingDate(e.target.value)}
              />
            </label>
            <br />
            </form>
            <button style={{marginTop: "10px"}} onClick={this.changeClosingDate.bind(this)}>
            Change Closing Date
            </button>
            {this.state.closingDateError ? (<div style={{color: "red"}}>{this.state.closingDateError}</div>)
             : this.state.closingDateMessage ? (<div style={{color: "green"}}>{this.state.closingDateMessage}</div>) 
             : (null)}
            </div>
            <br/>
            </React.Fragment>)}

            <div className="divider" style={{marginBottom: "20px"}}></div>

            {this.state.requiredSkillList ? (<div><h5>Required Skills</h5>
            <div className="row" style={{paddingTop: "45px"}}>{this.state.requiredSkillList.map(skill => 
              (<OpportunitySkill key={skill.skillID.id}
                        skillID={skill.skillID.id}
                        skillName={skill.name}
                        opportunityID={this.state.opportunityID}
                        reloadComponent={this.reloadComponent.bind(this)}
                        skill="required"></OpportunitySkill>))}</div> 
                        <div className="divider" style={{marginTop: "-30px", marginBottom: "20px"}}></div>
            </div>) : (null)}

           

            {this.state.desiredSkillList ? (<div ><h5>Desired Skills</h5>
            <div className="row" style={{paddingTop: "45px"}}>{this.state.desiredSkillList.map(skill => 
              (<OpportunitySkill key={skill.skillID.id}
                        skillID={skill.skillID.id}
                        skillName={skill.name}
                        opportunityID={this.state.opportunityID}
                        reloadComponent={this.reloadComponent.bind(this)}
                        skill="desired"
                        ></OpportunitySkill>))}</div> 
                        <div className="divider" style={{marginTop: "-30px", marginBottom: "20px"}}></div>
            </div>) : (null)}

           

            {this.state.studentLogin ? (null) : this.state.notSetSkillList ? (<div><h5>Add Skills to Opportunity</h5>
            <select style={{marginBottom: "10px"}} value={state.skillID} onChange={ e => this.updateSkillID(e.target.value) }>
                     {this.state.notSetSkillList.map(skill => {
                        return <option key={skill.skillID.id} value={skill.skillID.id}>{skill.name}</option>;
              })}</select><br></br>
            <button style={{marginLeft: "5px", marginRight: "5px"}} onClick={this.addRequiredSkill.bind(this)}>Add Skill as Required</button>
            <button style={{marginLeft: "5px", marginRight: "5px"}}onClick={this.addDesiredSkill.bind(this)}>Add Skill as Desired</button> 
            <br/><br/><div className="divider"></div><br/></div>) : (null)}

               
                <h5>Details:</h5>
                {this.state.details}<br /><br /><div className="divider"></div><br />
                 <h5>How to Apply</h5>
                 {this.state.howToApply}
                
            </div>
            ) : (<div>No organisation found</div>)}

            
            
</React.Fragment>)}
            </React.Fragment>
        );
    }
}
